body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

.header {
  display: flex;
}

.logo {
  align-items: center;
  display: flex;
}

.logo__img {
  width: 40px;
  height: 40px;
  margin-left: 50px;
}

.nav {
  justify-content: flex-end;
  width: 100%;
  margin-right: 50px;
  display: flex;
}

.nav__list {
  margin: 0;
  list-style-type: none;
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  height: 60px;
  margin-left: 20px;
  line-height: 60px;
}

a {
  color: #000;
  height: 60px;
  font-weight: 500;
  text-decoration: none;
  display: block;
}

.first__link, .other__link:hover {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  height: 4px;
  display: block;
  position: relative;
  bottom: 4px;
}

